<template>
  <div class="signupform">
    <div class="signupform__title">
      <p>
        <font-awesome-icon
          icon="user-plus"
          class="signupform__title__icon"
          style="color: #0075ff"
        />
        Sign up
      </p>
    </div>
    <div class="signupform__form">
      <div class="signupform__form_loading">
        <font-awesome-icon
          icon="spinner"
          class="signupform__form_loading_icon fa-spin"
        />
      </div>
      <AuthInput
        v-if="firstAuthInput"
        :type="firstAuthInput"
        @keydown.enter.native="currentStageAction"
        place="top"
        :busy="busy"
        extend="signup"
      />
      <InputWorm
        :top="topInputValue"
        :bottom="bottomInputValue"
        v-if="firstAuthInput && secondAuthInput"
        compare="true"
      />
      <AuthInput
        v-if="secondAuthInput"
        :type="secondAuthInput"
        @keydown.enter.native="currentStageAction"
        place="bottom"
        :busy="busy"
        extend="signup"
      />
      <Button
        :class="['signupform__form__button_submit', busy ? 'busy_btn' : '']"
        @click.native="currentStageAction"
        text="Continue"
        theme="primary_big"
      />
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import Button from './Button.vue';
import InputWorm from './InputWorm.vue';
import AuthInput from './AuthInput';

export default {
  name: 'SignUpForm',
  components: {
    Button,
    InputWorm,
    AuthInput,
  },
  props: ['show'],
  watch: {
    show(val) {
      this.visibleForm(val);
    },
  },
  data() {
    return {
      animToReset: [],
      busy: false,
      currentStageToken: '',
    };
  },
  computed: {
    ...mapGetters({
      signUpStage: 'popups/signUpStage',
      firstAuthInput: 'popups/firstSignUpInput',
      secondAuthInput: 'popups/secondSignUpInput',
      topInputValue: 'popups/firstSignUpInputValue',
      bottomInputValue: 'popups/secondSignUpInputValue',
    }),
  },
  methods: {
    currentStageAction() {
      const actionWrapper = action => {
        this.loading();
        action()
          .then(res => {
            this.loaded();
            this.handleSuccess(res.data);
            this.currentStageToken = res.data.token;

            if (this.signUpStage == 2) this.success(res.data.token);

            this.$store.commit('popups/SIGN_UP_NEXT');
          })
          .catch(err => {
            this.loaded();

            if (err.response) {
              this.handleError(err.response.data);

              if ([408, 409].includes(+err.response.status)) {
                this.$store.commit('popups/SIGN_UP_BACK');
              }

              if (this.signUpStage === 1) {
                this.$store.commit('popups/SIGN_UP_TOP_INPUT_CHANGED', '');
              }
            }
          });
      };

      switch (this.signUpStage) {
        case 0:
          actionWrapper(() =>
            this.axios.post(
              window.location.protocol +
                '//' +
                window.location.host +
                '/api/auth/confirmation/send',
              {
                email: this.topInputValue,
              },
            ),
          );
          break;
        case 1:
          actionWrapper(() =>
            this.axios.post(
              window.location.protocol +
                '//' +
                window.location.host +
                '/api/auth/confirmation/compare',
              {
                code: this.topInputValue,
              },
              {
                headers: {
                  Authorization: this.currentStageToken,
                },
              },
            ),
          );
          break;
        default:
          actionWrapper(() =>
            this.axios.post(
              window.location.protocol +
                '//' +
                window.location.host +
                '/api/auth/signup',
              {
                password: this.topInputValue,
                repeatPassword: this.bottomInputValue,
              },
              {
                headers: {
                  Authorization: this.currentStageToken,
                },
              },
            ),
          );
          break;
      }
    },
    handleError(response) {
      this.$store.commit('popups/ADD_ALERT', {
        type: 'error',
        title: response.stage,
        message: response.message,
      });
    },
    handleSuccess(response) {
      this.$store.commit('popups/ADD_ALERT', {
        type: 'message',
        title: response.stage,
        message: response.message,
      });
      location.reload();
    },
    loading() {
      this.busy = true;
      this.$el.querySelector('.signupform__form_loading').style.opacity = 1;
      this.$el.querySelector('.signupform__form_loading').style.pointerEvents =
        'all';
    },
    loaded() {
      this.busy = false;
      this.$el.querySelector('.signupform__form_loading').style.opacity = 0;
      this.$el.querySelector('.signupform__form_loading').style.pointerEvents =
        'none';
    },
    success(token) {
      this.axios
        .get(
          window.location.protocol + '//' + window.location.host + '/api/auth',
          {
            headers: {
              Authorization: token,
            },
          },
        )
        .then(response => {
          this.$store.commit('auth/SET_PROFILE', {
            token: response.data.token,
            profile: response.data.profile,
          });

          this.$store.commit('popups/CLEAR_AUTH_INPUTS');
        })
        .catch(err => {
          console.log(err);
        });

      this.close();
    },
    visibleForm(yes) {
      const self = this;

      if (yes) {
        this.$el.style.display = 'flex';

        this.$anime({
          targets: '.signupform__title',
          translateX: [-500, 0],
          opacity: [0, 1],
          duration: 300,
          easing: 'easeInOutSine',
        });

        this.$anime({
          targets: '.signupform__form',
          translateX: [500, 0],
          opacity: [0, 1],
          duration: 300,
          easing: 'easeInOutSine',
          update(anime) {
            if (anime.progress > 30) {
              self.$el.style.backgroundColor = '#00002299';
              self.$el.style.backdropFilter = 'blur(2px)';
            }
          },
        });
      } else {
        this.$el.style.backgroundColor = 'transparent';
        this.$el.style.backdropFilter = 'blur(0px)';

        this.$store.commit('popups/SIGN_UP_TOP_INPUT_CHANGED', '');
        this.$store.commit('popups/SIGN_UP_BOTTOM_INPUT_CHANGED', '');

        this.$anime({
          targets: '.signupform__title',
          translateX: [0, 500],
          opacity: [1, 0],
          duration: 300,
          easing: 'easeOutSine',
        });

        this.$anime({
          targets: '.signupform__form',
          translateX: [0, -500],
          opacity: [1, 0],
          duration: 300,
          easing: 'easeOutSine',
          complete() {
            self.$el.style.display = 'none';
          },
        });
      }
    },
    close() {
      this.$store.commit('popups/SIGN_UP', false);
      this.$store.commit('popups/CLEAR_AUTH_INPUTS');
    },
  },
  mounted() {
    this.$el.onclick = e => {
      if (e.target === this.$el) {
        this.close();
      }
    };

    document.addEventListener('keydown', e => {
      if (e.key === 'Escape') {
        this.close();
      }
    });
  },
};
</script>

<style lang="sass" scroped>
@import '../../scss/smart-grid'
@import '../../scss/variables'

*
  margin: 0
  padding: 0

.signupform
  position: absolute
  z-index: 888
  left: 0
  top: 0
  width: 100vw
  height: 100vh
  transition: backdrop-filter .3s
  transition: background-color .3s
  display: none
  flex-direction: column
  justify-content: center
  align-items: center
  background-color: transparent
  backdrop-filter: blur(0px)


  &__title
    top: -10px
    width: 500px
    position: relative
    background: linear-gradient(-45deg, $blue, lighten($blue, 7))
    border-radius: 10px 10px 0 0
    border: 1px $cyan solid
    color: white
    font-family: 'Montserrat'
    font-weight: 500
    padding: 10px 0
    opacity: 0
    transform: translateX(500px)

    @include xs-block
      width: 100%
      border-radius: 0
    @include sm-block
      width: 500px
      border-radius: 10px 10px 0 0

    &__icon
      margin-right: 3px

  &__form
    position: relative
    width: 500px
    padding-top: 40px
    background: $blue
    border-radius: 0 0 10px 10px
    border: 1px #0075ff33 solid
    display: flex
    flex-direction: column
    overflow: hidden
    opacity: 0
    transform: translateX(-500px)

    @include xs-block
      width: 100%
      border-radius: 0
    @include sm-block
      width: 500px
      border-radius: 0 0 10px 10px

    &_loading
      position: absolute
      width: 100%
      height: 100%
      top: 0
      left: 0
      z-index: 3
      overflow: hidden
      background-color: $blue-alpha
      opacity: 0
      pointer-events: none
      display: grid

      &_icon
        place-self: center
        width: 100px
        font-size: 4rem
        margin-bottom: 30px
        height: 80px
        color: $cyan

    &__button_submit
      opacity: 1
      margin: 40px auto
      width: 100px

    &__button_wallet, &__button_settings
      z-index: 100
      opacity: 0
      width: 100%
      pointer-events: none

    &__status
      position: absolute
      height: 80px
      left: calc(50% - 10rem)
      top: calc(50% - 75px)
      font-size: 2rem
      font-weight: 500
      font-family: 'Montserrat'
      color: white
      opacity: 0
      pointer-events: none

      &_icon
        transform: scale(0)

.busy_btn
  pointer-events: none
  opacity: .2
</style>
